// animation delays
.delay1 {
    animation-delay:1s;
    -webkit-animation-delay:1s;
}
.delay2 {
    animation-delay:2s;
    -webkit-animation-delay:2s;
}
.delay3 {
    animation-delay:3s;
    -webkit-animation-delay:3s;
}
.delay4 {
    animation-delay:4s;
    -webkit-animation-delay:4s;
}

// Restyled Primary Buttons

.btn-primary {
	.button-variant(white; @brand-primary; @brand-primary);
	.heading-font;
	font-weight: 700;
}

.btn-xl {
	.button-variant(white; @brand-primary; @brand-primary);
	.heading-font;
	font-weight: 700;
