body {
    webkit-tap-highlight-color: @brand-primary;
}

.portfolio-item {
  min-height: 330px;
}

.diploma {
	border: 3px @gray-lighter solid;
	margin: 1em 0;
	padding: 1em 1em .5em 1em;
	text-align: center;
	position: relative;
	i {
		color: @gray;
	}
	p {
		margin-top: .5em;
		vertical-align: middle;
	}
}

.report-card {
  text-align:center;
}

.badges {
  margin:10px 10px 25px;
  padding:0;
}

.badges li {
  display:inline-block;
  margin:2px;
}

.badges li img {
    width: 100%;
    text-align: center;
    -webkit-box-reflect: below 0 -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(70%, transparent), to(rgba(255,255,255,0.2)));
}

.badges li img:hover {
  opacity:.7;
  animation:badges 100ms;
  -webkit-animation:badges 100ms;
}

@keyframes badges {
  0% { margin: -17px 2px 0px 0px }
  50% { margin: -27px 2px 0px 0px }
  100% { margin: -17px 2px 0px 0px }
}
@-webkit-keyframes badges /* Safari and Chrome */ {
  0% { margin: -17px 2px 0px 0px }
  50% { margin: -27px 2px 0px 0px }
  100% { margin: -17px 2px 0px 0px }
}

.spinner {
  width: 40px;
  height: 40px;
  margin: 100px auto;
  background-color: #333;

  border-radius: 100%;
  -webkit-animation: scaleout 1.0s infinite ease-in-out;
  animation: scaleout 1.0s infinite ease-in-out;
}

@-webkit-keyframes scaleout {
  0% { -webkit-transform: scale(0.0) }
  100% {
    -webkit-transform: scale(1.0);
    opacity: 0;
  }
}

@keyframes scaleout {
  0% {
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
    } 100% {
      transform: scale(1.0);
      -webkit-transform: scale(1.0);
      opacity: 0;
    }
  }

// reponsive video player
.video-container {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 30px; height: 0; overflow: hidden;
    margin: 0 0 20px 0;
}
.video-container iframe,
.video-container object,
.video-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
